:global(dialog) {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 320px;
  min-height: 200px;
  padding: 10px;
  transform: translateX(-50%) translateY(-50%);

  border: 1px solid black;
  outline: none;
  &::backdrop {
    background-color: rgba(20, 20, 20, 0.156);
  }
}

.appMain {
  height: 100vh;
  background-color: teal;
}

.main {
  position: relative;
  height: calc(100vh - 40px);
  overflow-x: hidden;
}
