.authForm {
  position: relative;

  label {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 10px;
  }

  input {
    display: block;
    padding: 5px;
    font-size: 20px;
    font-size: 16px;

    &.error {
      background-color: rgba(255, 0, 0, 0.391);
    }
  }
}

.closeBtn {
  position: absolute;
  top: -5px;
  right: -5px;
  padding: 0;
  margin: 0;
  border: none;
  background-color: transparent;
}

.fieldsWrapper {
  padding: 5px;
}

.buttonsWrapper {
  display: flex;
  justify-content: space-between;
  padding: 5px;
}
