.commonButton {
  display: flex;
  align-items: center;
  padding: 5px;
  margin: 5px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.buttonIcon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  & + * {
    margin-left: 5px;
  }
}
